*,
*::before,
*::after {
  box-sizing: border-box;
}



.card-frame,
.card-number-frame,
.expiry-date-frame,
.cvv-frame {
  height: 38px;
}

#schemeChoice {
  height: 200px !important;
}

#payment-form {
  width: 280px;
  margin: 0 auto;
}

// label {
//   display: block;
//   height: 10px;
//   color: #13395e;
//   font-size: 14px;
//   font-weight: 500;
//   line-height: 10px;
//   margin: 8px 0;
// }

.date-and-code {
  display: flex;
  margin-bottom: 8px;
}

.date-and-code>div:nth-child(1) {
  width: 55.715%;
}

.date-and-code>div:nth-child(2) {
  width: 45.719%;
}

.input-container {
  position: relative;
  display: flex;
  height: 40px;
}

.icon-container:last-child {
  right: 0;
}

.icon-container.payment-method {
  right: 0;
}

.input-container.card-number {
  margin-bottom: 8px;
}

.input-container.expiry-date {
  margin-right: 4px;
}

.input-container.cvv {
  margin-left: 4px;
}

.card-number-frame {
  padding-left: 40px;
}

.expiry-date-frame {
  padding-left: 40px;
}

.cvv-frame {
  padding-left: 40px;
}

.icon-container {
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  width: 26px;
  margin: 0 7px;
}

.icon-container.payment-method {
  transform: translateY(-50%) rotateY(90deg);
  transition: opacity 0.15s ease-out;
  opacity: 0;
  top: 50%;
}

.icon-container.payment-method.show {
  opacity: 1;
  transition: all 0.4s ease-out;
  transform: translateY(-50%) rotateY(0deg);
}

.icon-container.payment-method img {
  width: 100%;
}

[id$="-error"] {
  display: none;
}

.frame {
  opacity: 0;
}

.frame--activated {
  opacity: 1;
  // border: solid 1px #13395e;
  background: #F4F4F4;
  border-radius: 0px;
  // box-shadow: 0 1px 3px 0 rgba(19, 57, 94, 0.2);
}

.frame--activated.frame--focus {
  // border: solid 1px #13395e;
  background: #F4F4F4;
  box-shadow: 0 2px 5px 0 rgba(19, 57, 94, 0.15);
}

.frame--activated.frame--invalid {
  border: solid 1px red;
  box-shadow: 0 2px 5px 0 rgba(217, 104, 48, 0.15);
}

.error-message {
  display: block;
  color: red;
  font-size: 0.9rem;
  margin: 8px 0 0 1px;
  font-weight: 300;
}

#pay-button {
  margin-top: 150px;
  border: none;
  border-radius: 3px;
  color: #fff;
  font-weight: 500;
  height: 40px;
  width: 100%;
  background-color: #13395e;
  box-shadow: 0 1px 3px 0 rgba(19, 57, 94, 0.4);
}

#pay-button:active {
  background-color: #0b2a49;
  box-shadow: 0 1px 3px 0 rgba(19, 57, 94, 0.4);
}

#pay-button:hover {
  background-color: #15406b;
  box-shadow: 0 2px 5px 0 rgba(19, 57, 94, 0.4);
}

#pay-button:disabled {
  background-color: #697887;
  box-shadow: none;
}

#pay-button:not(:disabled) {
  cursor: pointer;
}

.success-payment-message {
  color: #13395e;
  line-height: 1.4;
}

.token {
  color: #b35e14;
  font-size: 0.9rem;
  font-family: monospace;
}

/**
IE11-targeted optimisations
*/

_:-ms-fullscreen,
:root .icon-container {
  display: block;
}

_:-ms-fullscreen,
:root .icon-container img {
  top: 50%;
  -ms-transform: translateY(-50%);
  position: absolute;
}

_:-ms-fullscreen,
#icon-card-number,
_:-ms-fullscreen,
#icon-expiry-date,
_:-ms-fullscreen,
#icon-cvv {
  left: 7px;
}

#checkout-frames-card-number::-ms-clear {
  display: none;
}
.payment-method-logo-container {
  display: flex;
  min-width: 26px;
  margin-left: 10px !important;
  margin-right: 10px !important;
  justify-content: center;
  align-items: center;
}
.card-number {
  width: 100%;
  height: 40px;
  padding: 0px;
  border: none !important;
  border-bottom: 1px solid var(--cds-ui-04, #8d8d8d) !important;
  background-color: var(--cds-field-01, #f4f4f4) !important;
  color: var(--cds-text-01, #161616) !important;
  transition: background-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9), outline 70ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

.expiry-date {
  width: 55%;
  height: 40px;
  padding: 0 1rem;
  border: none !important;
  border-bottom: 1px solid var(--cds-ui-04, #8d8d8d) !important;
  background-color: var(--cds-field-01, #f4f4f4) !important;
  color: var(--cds-text-01, #161616) !important;
  transition: background-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9), outline 70ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

.security-code {
  width: 55%;
  height: 40px;
  padding: 0 1rem;
  border: none !important;
  border-bottom: 1px solid var(--cds-ui-04, #8d8d8d) !important;
  background-color: var(--cds-field-01, #f4f4f4) !important;
  color: var(--cds-text-01, #161616) !important;
  transition: background-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9), outline 70ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

[class$="--invalid"] {
  color: red !important;
  -webkit-text-fill-color: red !important;
}

@media screen and (max-width:600px){
  .card-number {
    width: 105%;
    height: 40px;
    padding: 0px;
    border: none !important;
    border-bottom: 1px solid var(--cds-ui-04, #8d8d8d) !important;
    background-color: var(--cds-field-01, #f4f4f4) !important;
    color: var(--cds-text-01, #161616) !important;
    transition: background-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9), outline 70ms cubic-bezier(0.2, 0, 0.38, 0.9);
  }
}